import React, { Suspense, lazy } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Main from './layouts/Main'; // fallback for lazy pages
import './static/css/main.scss'; // All of our styles

const { PUBLIC_URL } = process.env;

// Every route - we lazy load so that each page can be chunked
// NOTE that some of these chunks are very small. We should optimize
// which pages are lazy loaded in the future.
// const About = lazy(() => import('./pages/About'));
const Contact = lazy(() => import('./pages/Contact'));
const Acknowledgements = lazy(() => import('./pages/Acknowledgements'));
const Index = lazy(() => import('./pages/Index'));
const NotFound = lazy(() => import('./pages/NotFound'));
const Projects = lazy(() => import('./pages/Projects'));
const ResumeCalvin = lazy(() => import('./pages/ResumeCalvin'));
const ResumeJustin = lazy(() => import('./pages/ResumeJustin'));
const Timeline = lazy(() => import('./pages/Timeline'));

const App = () => (
  <BrowserRouter basename={PUBLIC_URL}>
    <Suspense fallback={<Main />}>
      <Switch>
        <Route exact path="/" component={Index} />
        {/* <Route path="/about" component={About} /> */}
        <Route path="/project" component={Projects} />
        <Route path="/timeline" component={Timeline} />
        <Route path="/contact" component={Contact} />
        <Route path="/acknowledgements" component={Acknowledgements} />
        <Route path="/resumeCalvin" component={ResumeCalvin} />
        <Route path="/resumeJustin" component={ResumeJustin} />
        <Route component={NotFound} status={404} />
      </Switch>
    </Suspense>
  </BrowserRouter>
);

export default App;
